@import '../mixin';
@import '../setup';

.container--header {
  position: relative;
}
.section--header {
  background: none;

  margin: 0 auto;
  position: relative;
}
.header-position {
  display: flex;
  max-width: 780px;
  &:last-child(2) {
    flex-direction: row-reverse;
  }
}
.container--background-Hero {
  background: none;
  margin: 0 auto;
  
}

.points {
  position: absolute;
  bottom: 0;
  right: 0;

  &__mobile {
    display: block;

    @include tablet {
      display: none;
    }
    @include desktop {
      display: none;
    }
  }

  &__tablet {
    display: none;

    @include tablet {
      display: block;
    }
    @include desktop {
      display: none;
    }
  }
  &__desktop {
    display: none;

    @include tablet {
      display: none;
    }
    @include desktop {
      display: block;
    }
  }
}

.header {
  display: flex;
  justify-content: space-between;
  background-color: getColor('background');

  &__svg {
    text-align: center;
  }

  &__list {
    display: none;

    @include desktop {
      display: flex;
      flex-direction: row;
      padding: 32px 0 13px 0;
      font-size: 16px;
      font-family: Montserrat;
      font-style: normal;
      font-weight: 700;
      gap: 28px;
      color: getColor('primary');
      transition-property: color;
      transition-duration: getColor('transition-duration');
      transition-timing-function: getColor('timing-function');
    }
  }
  &__link {
    text-decoration: none;
    color: getColor('text');
    transition-property: color;
    transition-duration: getColor('transition-duration');
    transition-timing-function: getColor('timing-function');
    &:hover,
    &:focus {
      color: getColor('primary');
    }
  }
  &__icon {
    fill: getColor('primary');
    width: 40px;
    height: 37px;
    margin-top: 12px;

    @include phone {
      width: 48px;
      height: 44px;
      margin-top: 14px;
    }
    @include tablet {
      margin-top: 20px;
    }
    @include desktop {
      width: 60px;
      height: 65px;
      margin-top: 8px;
    }
  }
}

.modal-btn {
  display: none;
  width: 152px;
  height: 42px;

  @include tablet {
    margin: 24px 32px 0 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    border-radius: 10px;
    background-color: getColor('primary');
    color: getColor('button_advantages');
    border: none;
    border-radius: 4px;
    letter-spacing: 0.06em;
    transition-property: color, background;
    transition-duration: getColor('transition-duration');
    transition-timing-function: getColor('timing-function');
    &:hover {
      background: getColor('background-header');
    }
    &:focus {
      fill: getColor('background_address');
      color: getColor('background_address');
      background: getColor('background-header');
    }
    &:disabled {
      color: getColor('time-color');
    }
    &__button-icon {
      padding-left: 4px;
    }
  }
}

// .modal-btn--modal-open {
//   padding: 12px 35px 12px 36px;
// }
.modal-btn--mobile {
  display: flex;
  width: 152px;
  height: 42px;
  align-items: center;
  justify-content: center;
  margin: 110px 84px 0 84px;
  border-radius: 10px;
  background-color: getColor('primary');
  color: getColor('button_advantages');
  text-decoration: solid;
  font-weight: 600;
  border: none;
  border-radius: 4px;
  letter-spacing: 0.06em;
  &:hover {
    background: getColor('background-header');
  }
  &:focus {
    fill: getColor('background_address');
    color: getColor('background_address');
    background: getColor('background-header');
  }
  &:disabled {
    color: getColor('time-color');
  }

  @include phone {
    margin: 114px 164px 88px 164px;
  }
  @include tablet {
    display: none;
  }
}

.header2 {
  display: flex;
  justify-content: space-between;

  &__title {
    color: getColor('text');
    font-family: Seymour One;
    font-size: 26px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 1.04px;
    text-transform: uppercase;
    margin-top: 50px;

    @include phone {
      margin-top: 40px;
    }

    @include desktop {
      font-size: 38px;
      font-weight: 400;
    }
  }
  &__list {
    display: none;

    @include tablet {
      display: flex;
      flex-direction: column;
      margin-top: 28px;
    }
  }
  &__number {
    color: getColor('body');
    text-align: right;
    font-family: Montserrat;
    font-size: 28px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
    padding-right: 32px;

    @include desktop {
      font-size: 46px;
      padding-right: 16px;
    }
  }
  &__item {
    color: getColor('text');
    text-align: right;
    font-family: Montserrat;
    font-size: 8px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding-right: 32px;

    @include desktop {
      font-size: 14px;
      padding-right: 16px;
    }
  }
  &__item-gap {
    @include tablet {
      margin-bottom: 20px;
    }
  }
}

.header3 {
  &__picture {
    position: absolute;
    right: 60px;
    width: 177px;
    height: 217px;
    bottom: 0;

    @include phone {
      top: 90px;
      right: 20px;
      width: 218px;
      height: 267px;
      bottom: 0;
    }

    @include tablet {
      top: inherit;
      width: 268px;
      height: 329px;
      right: 92px;
    }

    @include desktop {
      width: 456px;
      height: 560px;
    }
  }
  &__text {
    display: none;

    @include tablet {
      display: flex;
      color: getColor('background_address');
      font-family: DM Sans;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      margin: 46px 0 29px 0;
      width: 165px;
    }
    @include desktop {
      width: 200px;
      margin-top: 70px;
      font-size: 14px;
      margin: 120px 0 24px 0;
    }
  }

  &__icecream {
    display: none;

    @include tablet {
      display: flex;
      width: 152px;
      height: 152px;
      margin: 12px 0;
    }
    @include desktop {
      display: flex;
      width: 225px;
      height: 225px;
      margin: 42px 0 65px 0;
    }
  }
  &__elements {
    display: flex;
    flex-direction: column;
    text-align: left;
  }
  &__icon {
    display: none;
    fill: #fcfcfc;

    @include tablet {
      display: inline-flex;
      margin-bottom: 24px;
    }
  }
  &--position {
    display: flex;
    flex-direction: row;
    gap: 12px;

    @include tablet {
      padding-bottom: 12px;
    }

    @include desktop {
      gap: 16px;
    }
  }
}
.links {
  display: flex;
  flex-direction: row;
  gap: 12px;
  margin-top: 32px;
  padding-bottom: 236px;

  @include phone {
    align-items: baseline;
    padding-bottom: 157px;
  }

  @include tablet {
    padding-bottom: 0;
  }

  @include desktop {
    padding-bottom: 0;
  }

  &__products {
    width: 108px;
    height: 38px;
    color: #131313;
    text-align: center;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    border: 2px solid #f8d24e;
    border-radius: 8px;
    background: none;
    cursor: pointer;
    transition-property: color, border;
    transition-duration: getColor('transition-duration');
    transition-timing-function: getColor('timing-function');
    &:hover {
      border: 2px solid getColor('background-header');
    }
    &:focus {
      color: getColor('background_address');
      border: 2px solid getColor('background-header');
    }
    &:disabled {
      border: 2px solid getColor('time-color');
    }
  }
  &__made {
    width: 154px;
    height: 40px;
    color: #f8d24e;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration-line: underline;
    background: none;
    border: none;
    cursor: pointer;
    transition-property: color;
    transition-duration: getColor('transition-duration');
    transition-timing-function: getColor('timing-function');
    &:hover {
      color: getColor('background-header');
    }
    &:focus {
      color: getColor('background_address');
    }
    &:disabled {
      color: getColor('time-color');
    }

    @include phone {
      width: 100px;
      height: 24px;
    }
  }
}

.toggle-btn {
  background-color: transparent;
  border: none;
  align-content: end;
  margin-left: auto;

  @include tablet {
    display: flex;
    flex-direction: row;
    align-content: end;
    margin-top: 29px;
  }
  @include desktop {
    display: none;
  }

  &__icon {
    width: 32px;
    height: 32px;
    align-items: end;
  }
}
.header3__backgrd {
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 0;

  &__icon-T-left {
    display: none;
    @include phone {
      display: none;
    }
    @include tablet {
      left: 0;
      @include desktop {
        display: none;
      }

      &__icon-T-right {
        @include tablet {
          right: 0;
          @include phone {
            &--backgrd-tablet {
              display: none;
            }
          }
        }
      }
    }
  }
}
