* {
  outline: none;
  margin: 0;
  padding: 0;
}

body {
  color: #131313;
  font-family: Montserrat, sans-serif;
}

section {
  background-image: url("Backgroud1Mobile.8f8b84e0.jpg");
  background-position: center;
  background-repeat: repeat;
  padding-top: 60px;
}

ul {
  list-style: none;
}

.container {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
}

@media screen and (min-width: 786px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.products {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.products__txt {
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  font-style: normal;
  font-weight: 800;
}

.products__title {
  text-align: center;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  margin-bottom: 40px;
  font-family: Seymour One, sans-serif;
  font-size: 30px;
  font-weight: 400;
}

@media screen and (min-width: 786px) {
  .products__title {
    margin-bottom: 80px;
  }
}

@media screen and (min-width: 1200px) {
  .products__title {
    margin-bottom: 120px;
  }
}

.desserts {
  flex-direction: column;
  gap: 60px;
  margin: 0 auto;
  display: flex;
}

@media screen and (min-width: 786px) {
  .desserts {
    flex-direction: row;
    gap: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .desserts {
    flex-direction: row;
  }
}

.desserts__item {
  height: 100%;
  max-width: 280px;
  min-height: 480px;
  border-radius: 24px;
  position: relative;
}

@media screen and (min-width: 480px) {
  .desserts__item {
    max-width: 320px;
    min-height: 428px;
  }
}

@media screen and (min-width: 786px) {
  .desserts__item {
    max-width: 224px;
    min-height: 428px;
  }
}

@media screen and (min-width: 1200px) {
  .desserts__item {
    max-width: 370px;
    min-height: 536px;
  }
}

.desserts > .bg-icecream {
  background-color: #aec6a5;
}

.desserts > .bg-coffie {
  background-color: #efd478;
}

.desserts > .bg-milkshakes {
  background-color: #d6936d;
}

.desserts__img {
  max-width: 200px;
  max-height: 200px;
}

@media screen and (min-width: 786px) {
  .desserts__img {
    margin-bottom: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .desserts__img {
    margin-bottom: 30px;
  }
}

.desserts__card {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.desserts__card-title {
  text-align: center;
  letter-spacing: .8px;
  text-transform: uppercase;
  margin-bottom: 40px;
  font-size: 20px;
  font-weight: 700;
}

.desserts__card-txt {
  text-align: center;
  letter-spacing: .56px;
  padding: 0 40px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.76;
}

@media screen and (min-width: 786px) {
  .desserts__card-txt {
    padding: 0 26px;
  }
}

.desserts__card-btn {
  background-color: #0000;
  border: none;
  position: absolute;
  top: 224px;
}

@media screen and (min-width: 480px) {
  .desserts__card-btn {
    top: 198px;
  }
}

