:root {
  --main-font: "Montserrat", sans-serif;
  --cubic-bezier: cubic-bezier(.4, 0, .2, 1);
  --fon-color: #fcfcfc;
}

.modal-container {
  width: 100%;
  height: 100%;
  transition-property: opacity, visibility;
  transition-duration: .25s;
  -webkit-transition-timing-function: var(--cubic-bezier);
  transition-timing-function: var(--cubic-bezier);
  z-index: 999;
  background-color: #0003;
  position: fixed;
  top: 0;
  left: 0;
}

.franchise-button__close {
  cursor: pointer;
  border: #0000;
  border-radius: 50%;
  position: absolute;
  top: 10px;
  right: 12px;
}

.franchise-container {
  overflow: none;
